"use client";

import { useState, useEffect, useRef } from "react";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Separator } from "@/components/ui/separator";
import type { MedicalReport } from "@/types/medical";

export default function ReportPage({ id }: { id: string }) {
  const [report, setReport] = useState<MedicalReport | null>(null);
  const [messages, setMessages] = useState<
    { role: "user" | "assistant"; content: string }[]
  >([]);
  const [input, setInput] = useState("");
  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    // Simulating report fetch
    const fetchedReport: MedicalReport = {
      disease: "Calcium Deficiency",
      dosha_analysis: {
        vata: "severely imbalanced",
        pitta: "balanced",
        kapha: "balanced",
      },
      observations: [
        "Peeling nails and dry skin indicate calcium deficiency.",
        "Slow nail growth suggests systemic Vata imbalance.",
      ],
      recommendations: {
        diet: [
          "Include calcium-rich foods like milk, almonds, and leafy greens.",
          "Consume sesame seeds for bone health.",
        ],
        lifestyle: [
          "Avoid overexertion and prioritize restful sleep.",
          "Use warm mustard oil for skin hydration.",
        ],
        herbal_remedies: [
          "Ashwagandha for improving bone strength.",
          "Musta for aiding digestion and nutrient absorption.",
          "Licorice tea for reducing dryness.",
        ],
      },
    };
    setReport(report);
  }, [id]);

  const handleSendMessage = () => {
    if (!input.trim()) return;

    const newMessages = [
      ...messages,
      { role: "user", content: input },
      {
        role: "assistant",
        content:
          "I can help you understand your medical report. What would you like to know?",
      },
    ];
    setMessages(newMessages);
    setInput("");

    // Scroll to bottom
    setTimeout(() => {
      scrollRef.current?.scrollTo({
        top: scrollRef.current.scrollHeight,
        behavior: "smooth",
      });
    }, 100);
  };

  return (
    <div className="flex min-h-screen">
      {/* Chat Assistant */}
      <div className="w-1/2 p-4 border-r">
        <Card className="h-full flex flex-col">
          <CardHeader>
            <CardTitle>Medical Assistant</CardTitle>
          </CardHeader>
          <CardContent className="flex-1 flex flex-col">
            <ScrollArea className="flex-1 pr-4" ref={scrollRef}>
              {messages.map((message, index) => (
                <div
                  key={index}
                  className={`mb-4 ${
                    message.role === "assistant" ? "pl-4" : "pr-4"
                  }`}
                >
                  <div
                    className={`p-3 rounded-lg ${
                      message.role === "assistant"
                        ? "bg-muted"
                        : "bg-primary text-primary-foreground"
                    }`}
                  >
                    {message.content}
                  </div>
                </div>
              ))}
            </ScrollArea>
            <Separator className="my-4" />
            <div className="flex gap-2">
              <Input
                value={input}
                onChange={(e) => setInput(e.target.value)}
                placeholder="Ask about your report..."
                onKeyDown={(e) => e.key === "Enter" && handleSendMessage()}
              />
              <Button onClick={handleSendMessage}>Send</Button>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Medical Report */}
      <div className="w-1/2 p-4">
        <Card>
          <CardHeader>
            <CardTitle>Medical Report</CardTitle>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="relative h-64 border rounded-lg overflow-hidden">
              <Image
                src={`https://kanteentest.s3.amazonaws.com/image${id}`}
                alt="Medical scan"
                fill
                className="object-contain"
              />
            </div>

            <div>
              <h3 className="text-lg font-semibold mb-2">Diagnosis</h3>
              <p>{report.disease}</p>
            </div>

            <div>
              <h3 className="text-lg font-semibold mb-2">Dosha Analysis</h3>
              <div className="grid grid-cols-3 gap-4">
                {Object.entries(report.dosha_analysis).map(
                  ([dosha, status]) => (
                    <div key={dosha} className="text-center p-2 border rounded">
                      <div className="font-medium capitalize">{dosha}</div>
                      <div className="text-sm">{status}</div>
                    </div>
                  )
                )}
              </div>
            </div>

            <div>
              <h3 className="text-lg font-semibold mb-2">Observations</h3>
              <ul className="list-disc pl-5 space-y-1">
                {report.observations.map((observation, index) => (
                  <li key={index}>{observation}</li>
                ))}
              </ul>
            </div>

            <div>
              <h3 className="text-lg font-semibold mb-2">Recommendations</h3>

              <div className="space-y-4">
                <div>
                  <h4 className="font-medium mb-1">Diet</h4>
                  <ul className="list-disc pl-5 space-y-1">
                    {report.recommendations.diet.map((item, index) => (
                      <li key={index}>{item}</li>
                    ))}
                  </ul>
                </div>

                <div>
                  <h4 className="font-medium mb-1">Lifestyle</h4>
                  <ul className="list-disc pl-5 space-y-1">
                    {report.recommendations.lifestyle.map((item, index) => (
                      <li key={index}>{item}</li>
                    ))}
                  </ul>
                </div>

                <div>
                  <h4 className="font-medium mb-1">Herbal Remedies</h4>
                  <ul className="list-disc pl-5 space-y-1">
                    {report.recommendations.herbal_remedies.map(
                      (item, index) => (
                        <li key={index}>{item}</li>
                      )
                    )}
                  </ul>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
